import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, FormArray, AbstractControl, Validators } from '@angular/forms';

// tslint:disable-next-line:import-blacklist
import { Observable, Subscription, pipe } from 'rxjs';
import { map, startWith } from 'rxjs/operators';

import { PTOService } from '../services/pto.service';
import { ItemService } from '../services/item.service';
import { Item } from '../models/item';
import { Casuality } from '../models/casuality';
// import { ceccAOR } from '../models/ceccAOR';
import { MatAutocompleteSelectedEvent } from '@angular/material/autocomplete';

@Component({
  selector: 'app-pto-new',
  templateUrl: './pto-new.component.html',
  styleUrls: ['./pto-new.component.css']
})
export class PtoNewComponent implements OnInit, OnDestroy {
  ptoForm: FormGroup;
  casForm: FormGroup;
  controls: AbstractControl[];

  categories = ['JE (Civ)', 'JE (E/M)', 'JE(QS&C)', 'OS', 'AAO', 'UDC', 'SAA', 'LDC', 'JAA', 'STENO GDE-III',
   'STENO', 'DTMN', 'BS', 'SK', 'M-READER', 'CMD', 'DUFTRY', 'PEON', 'CHOW(O)', 'MALI', 'S-WALA'];
  templetes;
  stations: {'station': string, 'units': string[]}[];
  units: string[];
  stationsSubscription: Subscription;
  templetesSubscription: Subscription;
  casualitySubscription: Subscription;
  previewSubscription: Subscription;
  itemsSubscription: Subscription;
  // options: string[] = ['One', 'Two', 'Three'];
  filteredOption: Observable<Item[]>;
  filteredOptions: Observable<Item[]>[] = [];
  items: Item[];
  // casIndex = 0;
  constructor(private ptoService: PTOService, private itemService: ItemService) { }

  ngOnInit() {
    this.stationsSubscription = this.ptoService.getStations()
      .subscribe((stations: {'station': string, 'units': string[]}[]) => {
        this.stations = stations;
        this.units = this.getUnits(this.ptoService.pto.STATION);
        // console.log('Stations : ', stations);
    });
    this.templetesSubscription = this.ptoService.getTempletes().subscribe((temp) => {
        this.templetes = temp;
        // console.log('Templetes : ', this.templetes);
    });
    this.ptoForm = new FormGroup({
      'ptono': new FormControl(this.ptoService.pto.PTO_No, Validators.required),
      'year': new FormControl(this.ptoService.pto.YEAR, Validators.required),
      'unit': new FormControl(this.ptoService.pto.UNIT, Validators.required),
      'station': new FormControl(this.ptoService.pto.STATION, Validators.required),
      'date': new FormControl(this.ptoService.pto.DATE, Validators.required),
      'casualities': new FormArray([]),
    });
    this.controls = (<FormArray>this.ptoForm.get('casualities')).controls;

    this.itemsSubscription = this.itemService.getItems().subscribe(items => {
      this.items = items;
      for (const cas of this.ptoService.pto.CASUALITIES) {
        this.addCasuality(cas);
      }
    });

    this.casualitySubscription = this.ptoService.casuality.subscribe((casuality) => {
      this.addCasuality(<Casuality>casuality);
    });
    this.previewSubscription = this.ptoService.preview.subscribe(() => this.preview());
  }
  ngOnDestroy() {
    this.stationsSubscription.unsubscribe();
    this.templetesSubscription.unsubscribe();
    this.casualitySubscription.unsubscribe();
    this.previewSubscription.unsubscribe();
    this.itemsSubscription.unsubscribe();
  }
  addCasuality(cas: Casuality) {
    this.casForm = new FormGroup({
      'sno': new FormControl(cas.S_No, Validators.required),
      'mesno': new FormControl(cas.MES_No, Validators.required),
      'name': new FormControl(cas.NAME, Validators.required),
      'category': new FormControl(cas.CATEGORY, Validators.required),
      'date': new FormControl(cas.DATE, Validators.required),
      'type': new FormControl(cas.TYPE, Validators.required),
      'particular': new FormControl(cas.PARTICULAR, Validators.required)
    });
    (<FormArray>this.ptoForm.get('casualities')).push(this.casForm);
    this.filteredOption = this.casForm.get('mesno').valueChanges.pipe(
      startWith(''),
      map(value => this._filter(value))
    );
    this.filteredOptions.push(this.filteredOption);
  }
  private _filter(value: string): Item[] {
    console.log('value : ', value);
    return this.items.filter(item => item.MES_No.toString().indexOf(value) === 0);
  }
  onSelectOption(event: MatAutocompleteSelectedEvent, formControl) {
    console.log('Option : ', event.option);
    const casIndex = this.controls.indexOf(formControl);
    this.controls[casIndex].get('name').setValue(event.option.value.NAME);
    this.controls[casIndex].get('category').setValue(event.option.value.CATEGORY);
  }
  displayFn(item?: Item | string): string | undefined {
    console.log('item : ', item);
    return typeof item !== 'object' ? item.toString() : item.MES_No.toString();
    // return item ? item.MES_No.toString() : undefined;
  }
  preview() {
    // console.log(this.ptoForm);
    this.ptoService.pto.PTO_No = +this.ptoForm.value.ptono;
    this.ptoService.pto.YEAR = +this.ptoForm.value.year;
    this.ptoService.pto.UNIT = this.ptoForm.value.unit;
    this.ptoService.pto.STATION = this.ptoForm.value.station;
    this.ptoService.pto.DATE = this.getDateStr(this.ptoForm.value.date),
    this.ptoService.pto.CASUALITIES = [];
    // this.addCasuality();
    for (const control of (<FormArray>this.ptoForm.get('casualities')).controls) {
      const casuality: Casuality = {S_No: 0, MES_No: '', NAME: '', CATEGORY: '', DATE: '', PARTICULAR: ''};
      casuality.S_No = +control.value.sno;
      casuality.MES_No = typeof control.value.mesno === 'object' ? control.value.mesno.MES_No : control.value.mesno;
      casuality.NAME = control.value.name;
      casuality.CATEGORY = control.value.category;
      casuality.DATE = this.getDateStr(control.value.date);
      casuality.TYPE = control.value.type;
      casuality.PARTICULAR = control.value.particular;
      this.ptoService.pto.CASUALITIES.push(casuality);
    }
    localStorage.setItem('pto', JSON.stringify(this.ptoService.pto));
    console.log('Inside pto-new : ', this.ptoService.pto);
  }
  getDateStr(dtStr: string) {
    if (dtStr) {
      const dt = new Date(dtStr);
      const month = dt.getMonth() + 1;
      const monthStr = month < 10 ? '0' + month : '' + month;
      const date = dt.getDate();
      const dateStr = date < 10 ? '0' + date : '' + date;
      return dt.getFullYear() + '-' + monthStr + '-' + dateStr;
    } else {
      return dtStr;
    }
  }
  onSelect(formControl: FormGroup, particular: string) {
    formControl.patchValue({ particular: particular});
  }
  onSelectStation(stn: {'station': string, 'units': string[]}) {
    this.units = stn.units;
    if (this.units.length === 1) {
      this.ptoForm.get('unit').setValue(this.units[0]);
    }
  }
  deleteCas(formControl: FormGroup) {
    const casIndex = (<FormArray>this.ptoForm.get('casualities')).controls.indexOf(formControl);
    (<FormArray>this.ptoForm.get('casualities')).controls.splice(casIndex, 1);
    this.ptoService.pto.CASUALITIES.splice(casIndex, 1);
    this.filteredOptions.splice(casIndex, 1);
    console.log('After Delete-PTO : ', this.ptoService.pto);
  }
  getUnits(stn: string) {
    for (const station of this.stations) {
      if (stn === station.station) {
        return station.units;
      }
    }
  }
}



