import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgForm } from '@angular/forms';
import { MatSnackBar } from '@angular/material';
// tslint:disable-next-line:import-blacklist
import { Subscription } from 'rxjs';

import { PTOService } from '../services/pto.service';

@Component({
  selector: 'app-templetes',
  templateUrl: './templetes.component.html',
  styleUrls: ['./templetes.component.css']
})
export class TempletesComponent implements OnInit, OnDestroy {
  templetes: any;
  templeteSelected = {type: '', particular: '', id: ''};
  templeteSubscription: Subscription;
  constructor(private ptoService: PTOService, private snackBar: MatSnackBar) {}

  ngOnInit() {
    this.templeteSubscription = this.ptoService.getTempletes().subscribe((temp) => {
      this.templetes = temp;
      this.templeteSelected = this.templetes[0];
      console.log('Templetes : ', this.templetes);
    });
  }
  ngOnDestroy() {
    this.templeteSubscription.unsubscribe();
  }
  onSubmitType(form: NgForm) {
    console.log('Type Form : ', form);
    if (form.valid && form.value.type.trim()) {
      const count = this.templetes.push({'type': form.value.type.trim(), 'particular': ''});
      this.ptoService.addTemplete(this.templetes[count - 1]);
      form.reset();
    } else {
      this.snackBar.open('The "Type" Form Field cannot be Empty!', '', { duration: 2000, });
    }
  }
  onSelectType(event: MouseEvent, templete: any) {
    if (event.srcElement.innerHTML === 'close') {
      this.templetes.splice(this.templetes.indexOf(templete), 1);
      this.ptoService.deleteTemplete(templete);
      return;
    }
    this.templeteSelected = templete;
  }
  onSubmitParticular() {
    // const index = this.templetes.indexOf(this.templeteSelected);
    // this.templetes[index].particular = this.templeteSelected.particular;
    this.ptoService.setTemplete(this.templeteSelected);
  }

}



