import { Component } from '@angular/core';
import { MatSnackBar } from '@angular/material';

import { PTOService } from '../services/pto.service';
import { Casuality } from '../models/casuality';

@Component({
  selector: 'app-pto',
  templateUrl: './pto.component.html',
  styleUrls: ['./pto.component.css']
})
export class PtoComponent {
  page = 'list';
  fillerNav = ['List', 'New PTO', 'New Casuality', 'Publish...'];
  mesno: string;
  dateFrom: string;
  dateUpto: string;

  constructor(private ptoService: PTOService, private snackBar: MatSnackBar) {}

  onNavItemClick(navItem: string) {
    console.log('Item Clicked : ' + navItem);
    if (navItem === 'List') {
      this.page = 'list';
      this.ptoService.search.next({PTO_No: null, YEAR: null, UNIT: '', STATION: '', DATE: ''});
    }
    if (navItem === 'New PTO') {
      if (localStorage.getItem('pto')) {
        this.ptoService.pto = JSON.parse(localStorage.getItem('pto'));
      } else {
        this.ptoService.pto = {PTO_No: null, YEAR: null, UNIT: '', STATION: '', DATE: '', CASUALITIES: []};
      }
      this.page = 'new';
    }
    if (navItem === 'Edit PTO') {
      this.page = 'new';
    }
    if (navItem === 'Publish...') {
      this.ptoService.preview.next();
      if ((this.ptoService.pto.PTO_No) && (this.ptoService.pto.YEAR) &&
         this.ptoService.pto.UNIT && this.ptoService.pto.STATION && this.ptoService.pto.DATE) {
          this.page = 'preview';
      } else {
        this.snackBar.open('The PTO Form Fields cannot be left Blank!', '', { duration: 2000, });
      }
    }
    if (navItem === 'New Casuality') {
      const casuality: Casuality = {S_No: 0, MES_No: '', NAME: '', CATEGORY: '', DATE: '', PARTICULAR: ''};
      this.ptoService.casuality.next(casuality);
    }
    if (navItem === 'edit-units') {
      this.page = 'edit-units';
    }
    if (navItem === 'templetes') {
      this.page = 'templetes';
    }
    if (navItem === 'help') {
      this.page = 'help';
    }
  }
  searchCas() {
    this.page = 'cas';
    this.ptoService.isLoading = true;
    setTimeout(() => {
      this.dateFrom = this.getDateStr(this.dateFrom);
      this.dateUpto = this.getDateStr(this.dateUpto);
      this.ptoService.casSummary.next({mesno: this.mesno, from: this.dateFrom, upto: this.dateUpto});
    }, 1000);
  }
  getDateStr(dtStr: string) {
    if (dtStr) {
      const dt = new Date(dtStr);
      const month = dt.getMonth() + 1;
      const monthStr = month < 10 ? '0' + month : '' + month;
      const date = dt.getDate();
      const dateStr = date < 10 ? '0' + date : '' + date;
      return dt.getFullYear() + '-' + monthStr + '-' + dateStr;
    } else {
      return dtStr;
    }
  }
}




