import { Component, OnInit, OnDestroy, EventEmitter, Output } from '@angular/core';
// tslint:disable-next-line:import-blacklist
import { Subscription } from 'rxjs';

import { PTO } from '../models/pto';
import { PTOService } from '../services/pto.service';

@Component({
  selector: 'app-pto-list',
  templateUrl: './pto-list.component.html',
  styleUrls: ['./pto-list.component.css']
})
export class PtoListComponent implements OnInit, OnDestroy {
  ptos: PTO[];
  getSubscription: Subscription;
  searchSubscription: Subscription;
  sortSubscription: Subscription;
  ipSubscription: Subscription;
  sortUp = false;
  @Output() detailClicked = new EventEmitter<void>();
  showIP = false;
  constructor(public ptoService: PTOService) {}

  ngOnInit() {
    this.ptoService.isLoading = true;
    this.getSubscription = this.ptoService.getPtos().subscribe(ptos => {
      this.ptos = ptos;
      this.ptoService.ptosCount = this.ptos.length;
      this.ptoService.isLoading = false;
      console.log('Total ptos : ', this.ptos);
    });
    this.searchSubscription = this.ptoService.search.subscribe((pto: PTO) => {
      this.ptoService.filterPtos(pto).subscribe(ptos => {
        this.ptos = ptos;
        this.ptoService.ptosCount = this.ptos.length;
        this.ptoService.isLoading = false;
        console.log('Total search ptos : ' + this.ptos.length);
      });
    });
    this.sortSubscription = this.ptoService.sort.subscribe((index: number) => {
      const keys = ['PTO_No', 'YEAR', 'UNIT', 'STATION', 'DATE'];
      if (index > 1) {
        this.sortOnElement(keys[index]);
      } else {
        this.sortOnNumber(keys[index]);
      }
    });
    this.ipSubscription = this.ptoService.showIP.subscribe(() => this.showIP = !this.showIP);
  }
  ngOnDestroy() {
    this.getSubscription.unsubscribe();
    this.searchSubscription.unsubscribe();
    this.sortSubscription.unsubscribe();
    this.ipSubscription.unsubscribe();
  }
  sortOnNumber(key: string) {
    this.sortUp = !this.sortUp;
    if (this.sortUp) {
        this.ptos.sort(function(a, b){return a[key] - b[key]; });
    } else {
        this.ptos.sort(function(a, b){return b[key] - a[key]; });
    }
  }
  sortOnElement(key: string) {
    this.sortUp = !this.sortUp;
    if (this.sortUp) {
        this.ptos.sort(function(a, b){
            const x = a[key];
            const y = b[key];
            if (x < y) {return -1; }
            if (x > y) {return 1; }
            return 0;
        });
    } else {
        this.ptos.sort(function(a, b){
            const x = a[key];
            const y = b[key];
            if (x < y) {return 1; }
            if (x > y) {return -1; }
            return 0;
        });
    }
  }
  showDetails(event: MouseEvent, pto: PTO) {
    if (event.srcElement.innerHTML === 'close') {
          this.ptoService.deletePto(pto.id).then(() => console.log('PTO Deleted'));
          return;
    }
    this.detailClicked.emit();
    this.ptoService.pto = pto;
  }

}



