import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { MatSnackBar } from '@angular/material';
// tslint:disable-next-line:import-blacklist
import { Subscription } from 'rxjs';

import { Item } from '../../models/item';
import { ItemService } from '../../services/item.service';
import { PTOService } from '../../services/pto.service';

@Component({
  selector: 'app-add-item',
  templateUrl: './add-item.component.html',
  styleUrls: ['./add-item.component.css']
})
export class AddItemComponent implements OnInit, OnDestroy {
  sortCol: number;
  sortUp = false;
  searchStr: string;
  categories = ['JE (Civ)', 'JE (E/M)', 'JE(QS&C)', 'OS', 'AAO', 'UDC', 'SAA', 'LDC', 'JAA',
                                         'STENO GDE-III', 'STENO', 'DTMN', 'BS', 'SK', 'CHOW(O)'];
  logoPath = 'https://cecc-staff.github.io/assets/MES_Logo.png';
  item: Item = {
    MES_No: null,
    NAME: '',
    CATEGORY: '',
    DOB: '',
    DOJ: '',
    UNIT: '',
    UNIT_SENIORITY: '',
    STATION: '',
    STN_SENIORITY: '',
    REMARKS: ''
  };
  @Input()user = null;
  stations: {}[];
  units: string[];
  stationSubscription: Subscription;
  constructor(public itemService: ItemService, private snackBar: MatSnackBar, public ptoService: PTOService) {}

  ngOnInit() {
    this.stationSubscription = this.ptoService.getStations().subscribe(stations => {
      this.stations = stations;
      // console.log('Stations : ', stations);
    });
  }
  ngOnDestroy() {
    this.stationSubscription.unsubscribe();
  }
  onSelectStation(stn: {'station': string, 'units': string[]}) {
    if (stn) {
      this.item.STATION = stn.station;
      this.item.UNIT = '';
      this.units = stn.units;
    } else {
      this.item.STATION = '';
      this.item.UNIT = '';
      this.units = [];
    }
  }
  onSubmit() {
    this.itemService.isLoading = true;
    const numItem = +this.item.MES_No;
    if (numItem && (numItem < numItem + 1)) {
      console.log('+this.item.MES_No : ', +this.item.MES_No);
      this.item.MES_No = +this.item.MES_No;
    } else {
      console.log('this.item.MES_No : ', this.item.MES_No);
    }
    this.setDates();
    this.itemService.search.next(this.item);
    console.log(this.item);
    let str = '';
    if (this.item.MES_No) { str += 'MES No. = ' + this.item.MES_No; }
    str += this.item.MES_No ? ', ' : '';
    if (this.item.NAME) { str += 'Name = ' + this.item.NAME; }
    str += this.item.NAME ? ', ' : '';
    if (this.item.CATEGORY) { str += 'Category = ' + this.item.CATEGORY; }
    str += this.item.CATEGORY ? ', ' : '';
    if (this.item.DOB) { str += 'Date of Birth <= ' + this.item.DOB; }
    str += this.item.DOB ? ', ' : '';
    if (this.item.DOJ) { str += 'Date of Joining <= ' + this.item.DOJ; }
    str += this.item.DOJ ? ', ' : '';
    if (this.item.UNIT) { str += 'Unit = ' + this.item.UNIT; }
    str += this.item.UNIT ? ', ' : '';
    if (this.item.UNIT_SENIORITY) { str += 'Unit Seniority <= ' + this.item.UNIT_SENIORITY; }
    str += this.item.UNIT_SENIORITY ? ', ' : '';
    if (this.item.STATION) { str += 'Station = ' + this.item.STATION; }
    str += this.item.STATION ? ', ' : '';
    if (this.item.STN_SENIORITY) { str += 'Station Seniority <= ' + this.item.STN_SENIORITY; }
    str += this.item.STN_SENIORITY ? ', ' : '';
    if (this.item.REMARKS) { str += 'Remarks = ' + this.item.REMARKS; }
    this.searchStr = str;
    this.sortCol = -1;
    this.resetItem();
  }
  onAdd() {
    this.itemService.isLoading = true;
    this.setDates();
    console.log('this.item : ', this.item);
    if (this.item.MES_No && this.item.NAME && this.item.CATEGORY && this.item.UNIT && this.item.STATION) {
      this.itemService.addItem(this.item).then((docRef) => {
        console.log('Document written with ID: ', docRef.id);
        this.resetItem();
        this.itemService.isLoading = false;
      });
    } else {
      this.itemService.isLoading = false;
      this.snackBar.open('Adding Blank Record is not Allowed !', '', { duration: 2000, });
    }
  }
  setDates() {
    this.item.DOB = this.getDateStr(this.item.DOB);
    this.item.DOJ = this.getDateStr(this.item.DOJ);
    this.item.UNIT_SENIORITY = this.getDateStr(this.item.UNIT_SENIORITY);
    this.item.STN_SENIORITY = this.getDateStr(this.item.STN_SENIORITY);
  }
  resetItem() {
    this.item = {MES_No: null, NAME: '', CATEGORY: '', DOB: '', DOJ: '', UNIT: '', UNIT_SENIORITY: '',
                 STATION: '', STN_SENIORITY: '', REMARKS: '' };
  }
  getDateStr(dtStr: string) {
    if (dtStr) {
      const dt = new Date(dtStr);
      const month = dt.getMonth() + 1;
      const monthStr = month < 10 ? '0' + month : '' + month;
      const date = dt.getDate();
      const dateStr = date < 10 ? '0' + date : '' + date;
      return dt.getFullYear() + '-' + monthStr + '-' + dateStr;
    } else {
      return dtStr;
    }
  }
  onSort(index: number) {
    this.itemService.isLoading = true;
    this.sortCol = index;
    this.sortUp = !this.sortUp;
    this.itemService.sort.next(index);
  }

}



