import { Component, Output, EventEmitter, OnDestroy, OnInit } from '@angular/core';
import jsPDF from 'jspdf/dist/jspdf.min';
import { MatSnackBar } from '@angular/material';
// tslint:disable-next-line:import-blacklist
import { Subscription } from 'rxjs';

import { PTO } from '../models/pto';
import { PTOService } from '../services/pto.service';
import { Http } from '@angular/http';

@Component({
  selector: 'app-pto-preview',
  templateUrl: './pto-preview.component.html',
  styleUrls: ['./pto-preview.component.css']
})
export class PtoPreviewComponent implements OnInit, OnDestroy {
  @Output() goBack = new EventEmitter<string>();
  filterSubscription: Subscription;
  time: string;

  constructor(public ptoService: PTOService, private snackBar: MatSnackBar, private http: Http) { }
  ngOnInit() {
    if (this.ptoService.pto.TIME) {
      const date = new Date(this.ptoService.pto.TIME);
      this.time = date.toDateString() + '/' + date.toLocaleTimeString();
    } else {
      this.time = 'Not Found';
    }
  }
  publish() {
    this.ptoService.isLoading = true;
    let showSnackBar = true;
    if ((this.ptoService.pto.PTO_No !== 0) && (this.ptoService.pto.YEAR !== 0) &&
      this.ptoService.pto.UNIT && this.ptoService.pto.STATION && this.ptoService.pto.DATE) {
          this.filterSubscription = this.ptoService.filterPtos(this.ptoService.pto).subscribe(ptos => {
              console.log('ptos.length : ', ptos.length);
              if (ptos.length === 0) {
                  showSnackBar = false;
                  this.http.get('https://ipapi.co/json')
                    .subscribe((res) => {
                      const data = res.json();
                      console.log('pto before : ', { ...this.ptoService.pto });
                      this.ptoService.pto.CITY = data.city;
                      this.ptoService.pto.IP = data.ip;
                      this.ptoService.pto.TIME = new Date().toString();
                      console.log('pto after: ', this.ptoService.pto);
                      this.ptoService.addPto().subscribe(() => {
                        localStorage.clear();
                        this.ptoService.pto = {
                          PTO_No: null,
                          YEAR: null,
                          UNIT: '',
                          STATION: '',
                          DATE: '',
                          CASUALITIES: []
                        };
                        this.back('List');
                      });
                    });
              } else {
                if (showSnackBar) {
                  console.log('This PTO is already Published');
                  this.ptoService.isLoading = false;
                  this.snackBar.open('This PTO is already Published !', '', { duration: 2000, });
                }
              }
          });
    }
  }
  ngOnDestroy() {
    if (this.filterSubscription) {
      this.filterSubscription.unsubscribe();
    }
  }
  back(navItem: string) {
    this.goBack.emit(navItem);
  }
  print() {
    jsPDF.API.hline = function(x, y, width){
      this.lines([[width, 0]], x, y, [1, 1], 'S', false);
    };

    jsPDF.API.heading = function(pto: PTO){
      this.setFontSize(16);
      this.text(['Part-II Order', '       By', pto.UNIT + ' ' + pto.STATION], 90, 7);
      this.setLineWidth(0.3);
      this.hline(15, 38, 180);
      this.hline(15, 55, 180);

      this.setFontSize(10);
      this.text('UNIT : ' + pto.UNIT, 15, 30);
      this.text('STATION : ' + pto.STATION, 15, 35);
      this.text('S.No. : ' + pto.PTO_No + '/' + pto.YEAR, 165, 30);
      this.text('DATE : ' + pto.DATE, 165, 35);

      this.text('S.No.', 15, 48);
      this.text('MES No., Name & Category', 30, 48);
      this.text('Date', 80, 48);
      this.text('Particulars', 130, 48);
    };

    const doc = new jsPDF();
    doc.heading(this.ptoService.pto);
    let partY = 60;
    for (const cas of this.ptoService.pto.CASUALITIES) {
      const arrPart = this.split(cas.PARTICULAR, 55);
      doc.text(cas.S_No + '', 20, partY);
      doc.text([cas.MES_No + '', cas.NAME, cas.CATEGORY], 30, partY);
      doc.text(cas.DATE, 75, partY);
      doc.text(arrPart, 100, partY);
      partY = arrPart.length < 3 ? partY + 15 : partY + arrPart.length * 5;
      if (partY > 260) {
        doc.addPage();
        doc.heading(this.ptoService.pto);
        partY = 60;
      }
    }
    doc.save(this.ptoService.pto.UNIT + '-' + this.ptoService.pto.STATION + '-' +
             this.ptoService.pto.PTO_No + '-' + this.ptoService.pto.YEAR + '.pdf');
  }

  split(str: string, segment: number) {
    let index = 0;
    const arrStr: string[] = [];
    let part: string;
    while (index < str.length) {
      part = str.substr(index, segment);
      arrStr.push(part);
      index += segment;
    }
    return arrStr;
  }

}



