import { Component, OnInit, OnDestroy } from '@angular/core';
// tslint:disable-next-line:import-blacklist
import { Subscription } from 'rxjs';

import { PTO } from '../models/pto';
import { PTOService } from '../services/pto.service';

@Component({
  selector: 'app-pto-search',
  templateUrl: './pto-search.component.html',
  styleUrls: ['./pto-search.component.css']
})
export class PtoSearchComponent implements OnInit, OnDestroy {
  sortCol: number;
  sortUp: boolean;
  pto: PTO = {
    PTO_No: null,
    YEAR: null,
    UNIT: '',
    STATION: '',
    DATE: ''
  };
  stations: {}[];
  units: string[];
  getSubscription: Subscription;
  searchSubscription: Subscription;
  constructor(public ptoService: PTOService) { }

  ngOnInit() {
    this.getSubscription = this.ptoService.getStations().subscribe(stations => {
      this.stations = stations;
      // console.log('Stations : ', stations);
    });
    this.searchSubscription = this.ptoService.search.subscribe((pto: PTO) => this.pto = pto);
  }
  ngOnDestroy() {
    this.getSubscription.unsubscribe();
    this.searchSubscription.unsubscribe();
  }
  onSubmit() {
    this.ptoService.isLoading = true;
    this.pto.PTO_No = this.pto.PTO_No ? +this.pto.PTO_No : null;
    this.pto.YEAR = this.pto.YEAR ? +this.pto.YEAR : null;
    this.pto.DATE = this.getDateStr(this.pto.DATE);

    this.ptoService.search.next(this.pto);
    console.log('Search PTO : ', this.pto);
    this.sortCol = -1;

    // this.ptoService.addStations();     //  For Adding Stations to Cloud Firestore Database
    // this.ptoService.addTempletes();    //  For Adding PTO Templetess to Cloud Firestore Database
  }
  onSelectStation(stn: {'station': string, 'units': string[]}) {
    if (stn) {
      this.pto.STATION = stn.station;
      this.pto.UNIT = '';
      this.units = stn.units;
    } else {
      this.pto.STATION = '';
      this.pto.UNIT = '';
      this.units = [];
    }
  }
  getDateStr(dtStr: string) {
    if (dtStr) {
      const dt = new Date(dtStr);
      const month = dt.getMonth() + 1;
      const monthStr = month < 10 ? '0' + month : '' + month;
      const date = dt.getDate();
      const dateStr = date < 10 ? '0' + date : '' + date;
      return dt.getFullYear() + '-' + monthStr + '-' + dateStr;
    } else {
      return dtStr;
    }
  }
  onSort(index: number) {
    this.sortCol = index;
    this.sortUp = !this.sortUp;
    this.ptoService.sort.next(index);
  }
  toggleIP() {
    console.log('toggleIP()');
    this.ptoService.showIP.next();
  }

}



