import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
// tslint:disable-next-line:import-blacklist
import { Observable, Subject } from 'rxjs';
// import { Subject } from 'rxjs/Subject';
import { CollectionReference, Query } from '@firebase/firestore-types';
import { Http } from '@angular/http';

import { PTO } from '../models/pto';
// import { ceccAOR } from '../models/ceccAOR';
// import { templetes } from '../models/templetes';

@Injectable()
export class PTOService {
    search = new Subject();
    sort = new Subject();
    casuality = new Subject();
    preview = new Subject();
    casSummary = new Subject();
    showIP = new Subject();
    ptos: Observable<PTO[]>;
    ptoCollection: AngularFirestoreCollection<PTO>;
    pto: PTO = {
        PTO_No: null,
        YEAR: null,
        UNIT: '',
        STATION: '',
        DATE: '',
        CASUALITIES: []
      };
    isLoading: boolean;
    ptosCount: number;
    // stations = Object.keys(ceccAOR);

    constructor(public afs: AngularFirestore) {
        this.ptoCollection = this.afs.collection('pto', ref => ref.orderBy('DATE', 'desc'));
        // this.ptos = this.ptoCollection.valueChanges();
        this.ptos = this.ptoCollection.snapshotChanges().map(changes => {
            console.log('changes : ', changes);
            return changes.map(a => {
                const data = a.payload.doc.data() as PTO;
                data.id = a.payload.doc.id;
                // console.log('Get PTOs - data : ' + JSON.stringify(data));
                return data;
            });
        });
        // afs.collection('pto').auditTrail().subscribe(console.log);
    }
    getPtos() {
        return this.ptos;
    }
    addPto() {
        return Observable.fromPromise(this.ptoCollection.add(this.pto));
    }
    filterPtos(pto: PTO) {
        console.log('Filtering PTOs');
        this.ptoCollection = this.afs.collection('pto',
            ref => {
              let query: CollectionReference | Query = ref;
              if (pto.PTO_No) { query = query.where('PTO_No', '==', pto.PTO_No); }
              if (pto.YEAR) { query = query.where('YEAR', '==', pto.YEAR); }
              if (pto.UNIT) { query = query.where('UNIT', '==', pto.UNIT); }
              if (pto.STATION) { query = query.where('STATION', '==', pto.STATION); }
              if (pto.DATE) { query = query.where('DATE', '==', pto.DATE); }
              return query;
            }
        );
        return this.ptoCollection.valueChanges();
    }
    deletePto(id: string) {
        return this.afs.doc<PTO>('pto/' + id).delete();
    }
    /*  Method for Adding PTO Templetes to database from file "templetes.ts"  */
    // addTempletes() {
    //     for (const templete of templetes) {
    //         this.afs.collection('templetes').add({'type': templete.viewValue, 'particular': templete.value});
    //     }
    // }
    addTemplete(templete: any) {
        this.afs.collection('templetes').add({'type': templete.type, 'particular': templete.particular});
    }
    deleteTemplete(templete: any) {
        this.afs.doc('templetes/' + templete.id).delete();
    }
    setTemplete(templete: any) {
        this.afs.doc('templetes/' + templete.id)
            .set({'type': templete.type, 'particular': templete.particular}).then(function() {
                console.log('Templete set : ', templete);
        });
    }
    getTempletes() {
        return this.afs.collection('templetes').snapshotChanges().map(changes => {
            return changes.map(a => {
                const data = a.payload.doc.data();
                data.id = a.payload.doc.id;
                return data;
            });
      });
    }
    /*  Method for Adding Stations & Units to database from file "ceccAOR.ts"  */
    // addStations() {
    //     for (const station of this.stations) {
    //         this.afs.collection('stations').add({'station': station, 'units': ceccAOR[station]});
    //     }
    // }
    addStation(stn: any) {
        this.afs.collection('stations').add({'station': stn.station, 'units': stn.units}).then(function(docRef) {
            console.log('Station added with ID: ', docRef.id);
        });
    }
    deleteStation(stn: any) {
        this.afs.doc('stations/' + stn.id).delete();
    }
    setStation(stn: any) {
        this.afs.doc('stations/' + stn.id).set({'station': stn.station, 'units': stn.units}).then(function() {
            console.log('Station set : ', stn);
        });
    }
    getStations() {
        return this.afs.collection('stations', ref => ref.orderBy('station')).valueChanges();
    }
    getStationsWithId() {
        return this.afs.collection('stations', ref => ref.orderBy('station')).snapshotChanges().map(changes => {
              return changes.map(a => {
                  const data = a.payload.doc.data();
                  data.id = a.payload.doc.id;
                  return data;
              });
        });
    }
}



